<!--
 * @Description: 播放多个flv视频流弹窗 页面
 * @Author: mhf
 * @Date: 2023-05-13 10:00:16
-->
<template>
  <el-dialog
    :title="videoPlayTitle"
    :visible="dialogVisible"
    :close-on-click-modal="false"
    :append-to-body="true"
    width="50%"
    v-drag-height
    v-drag-width
    v-el-drag-dialog
    :before-close="hideDialog"
  >
    <div>
      <div class="manyPlayDialog-video">
        <div
          class="manyPlayDialog-video-box"
          v-for="(item, index) in videoArr"
          :key="index"
        >
          <video :id="'videoElementE' + index" controls autoplay muted></video>
        </div>
      </div>
    </div>
  </el-dialog>
</template>

<script>
import flvjs from "flv.js";

export default {
  name: "manyPlayDialog",
  components: {},
  props: {
    videoArr: {
      type: Array,
      default: () => {
        return [];
      },
    }, // 格式： ["url1", "url2", ...]
    videoPlayTitle: "",
  },
  data() {
    return {
      dialogVisible: false,
    };
  },
  methods: {
    hideDialog() {
      this.dialogVisible = false;
    },
    showDialog() {
      this.dialogVisible = true;
      if (this.videoArr) {
        this.videoArr.forEach((item, index) => {
          setTimeout(() => {
            if (flvjs.isSupported()) {
              this.$forceUpdate();
              this.$nextTick(() => {
                var videoElementE = document.getElementById(
                  "videoElementE" + index
                );
                console.log(videoElementE, "videoElementE" + index);
                this.flvPlayer = flvjs.createPlayer({
                  type: "flv",
                  isLive: true,
                  hasAudio: false,
                  url: item,
                });
                this.flvPlayer.attachMediaElement(videoElementE);
                this.flvPlayer.load();
                this.flvPlayer.play();
              });
            }
          }, 500);
        });
      }
    },
  },
  created() {},
  mounted() {},
};
</script>

<style lang="scss" scoped>
::v-deep .manyPlayDialog {
  width: 100%;
  border: 1px solid red;

  &-video {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    &-box {
      width: 48%;
      height: 230px;
      margin-top: 10px;

      video {
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>
