<template>
  <div>
    <backHomeBtn>
      <template>
        <video
          id="videoElement"
          controls
          autoplay
          muted
          width="300px"
          height="200px"
        ></video>

        <el-button @click="play" type="primary" class="singlePlay"
          >播放</el-button
        >

        <hr />

        <el-button type="" @click="showManyPlayDialog"> 播放多个 </el-button>
        <manyPlayDialog
          ref="manyPlayDialog"
          :videoPlayTitle="videoPlayTitle"
          :videoArr="videoArr"
        />
      </template>
    </backHomeBtn>
  </div>
</template>

<script>
import flvjs from "flv.js";
import manyPlayDialog from "@/components/flvPlayer/manyPlayDialog";
export default {
  name: "flvComp",
  components: { manyPlayDialog },
  props: {},
  data() {
    return {
      flvPlayer: null,
      videoPlayTitle: "视频播放",
      videoArr: [
        "https://sf1-hscdn-tos.pstatp.com/obj/media-fe/xgplayer_doc_video/flv/xgplayer-demo-360p.flv",
        "https://sf1-hscdn-tos.pstatp.com/obj/media-fe/xgplayer_doc_video/flv/xgplayer-demo-360p.flv",
        "https://sf1-hscdn-tos.pstatp.com/obj/media-fe/xgplayer_doc_video/flv/xgplayer-demo-360p.flv",
      ],
    };
  },
  methods: {
    play() {
      this.flvPlayer.play();
    },

    /**
     * @Event 方法
     * @description: 展示播放多个的弹窗
     * */
    showManyPlayDialog() {
      this.$refs.manyPlayDialog.showDialog();
    },
  },
  created() {},
  mounted() {
    let res1 = {
      result: "success",
      slist: [
        {
          "http-flv":
            "http://36.134.4.226:10080/live/860773041100098_0001.live.flv",
          "http-hls":
            "http://36.134.4.226:10080/live/860773041100098_0001/hls.m3u8",
          "ws-flv":
            "ws://36.134.4.226:10080/live/860773041100098_0001.live.flv",
        },
        {
          "http-flv":
            "http://36.134.4.226:10080/live/860773041100096_0001.live.flv",
          "http-hls":
            "http://36.134.4.226:10080/live/860773041100096_0001/hls.m3u8",
          "ws-flv":
            "ws://36.134.4.226:10080/live/860773041100096_0001.live.flv",
        },
      ],
    }; // 丽水山区应急项目设备在线视频

    if (flvjs.isSupported()) {
      var videoElement = document.getElementById("videoElement");
      this.flvPlayer = flvjs.createPlayer({
        type: "flv",
        isLive: true,
        hasAudio: false,
        url: "https://sf1-hscdn-tos.pstatp.com/obj/media-fe/xgplayer_doc_video/flv/xgplayer-demo-360p.flv", // 自己的flv视频流
        // url: "http://36.134.4.226:10080/live/860773041100098_0001.live.flv",
        // url: "http://36.139.62.111:8081/live/860773041100098_0001.live.flv", // 可用的播放地址
      });
      this.flvPlayer.attachMediaElement(videoElement);
      this.flvPlayer.load();
      this.flvPlayer.play();
    }
  },
  destroyed() {
    this.flvPlayer.pause();
    this.flvPlayer.unload();
    this.flvPlayer.detachMediaElement();
    this.flvPlayer.destroy();
    this.flvPlayer = null;
  },
};
</script>

